import React from 'react';
import { ModalRoot, ModalCard } from '@vkontakte/vkui';

// Redux
import { connect } from 'react-redux';
import { openModal } from '../redux/actions/modal';

import AreAtHome from './AreAtHome';
import NotAllowLocation from './NotAllowLocation';
import NotAllowMessages from './NotAllowMessages';
import Reward from './Reward';
import Skipped from './Skipped';
import NotHome from './NotHome';
import NoGeodata from './NoGeodata';
import RankUp from './RankUp'

export const MODAL_ARE_AT_HOME = 'modal_are-at-home';
export const MODAL_NOT_ALLOW_MESSAGES = 'modal_not-allow-messages';
export const MODAL_NOT_ALLOW_LOCATION = 'modal_not-allow-location';
export const MODAL_REWARD = 'modal_reward';
export const MODAL_SKIPPED = 'modal_skipped';
export const MODAL_NOT_HOME = 'modal_not-home';
export const MODAL_NO_GEODATA = 'modal_no-geodata';
export const MODAL_RANK_UP = 'nodal_rank-up';

class Modal extends React.Component {

  setActiveModal(activeModal) {
    return this.props.onOpenModal(activeModal);
  }

  close() {
    return this.setActiveModal(null);
  }

  render() {
    return (
      <ModalRoot
        activeModal={this.props.activeModal || null}
        onClose={() => {this.close()}}
      >
        {this.props.activeModal === MODAL_ARE_AT_HOME ? AreAtHome(() => {this.close()}, this.props.permission) : <ModalCard id={MODAL_ARE_AT_HOME} />}
        {this.props.activeModal === MODAL_NOT_ALLOW_LOCATION ? NotAllowLocation(() => {this.close()}, this.props.permission) : <ModalCard id={MODAL_NOT_ALLOW_LOCATION} />}
        {this.props.activeModal === MODAL_NOT_ALLOW_MESSAGES ? NotAllowMessages(() => {this.close()}, this.props.permission) : <ModalCard id={MODAL_NOT_ALLOW_MESSAGES} />}
        {this.props.activeModal === MODAL_REWARD ? Reward(() => {this.close()}, this.props.modalParams) : <ModalCard id={MODAL_REWARD} />}
        {this.props.activeModal === MODAL_SKIPPED ? Skipped(() => {this.close()}, this.props.modalParams) : <ModalCard id={MODAL_SKIPPED} />}
        {this.props.activeModal === MODAL_NOT_HOME ? NotHome(() => {this.close()}) : <ModalCard id={MODAL_NOT_HOME} />}
        {this.props.activeModal === MODAL_NO_GEODATA ? NoGeodata(() => {this.close()}) : <ModalCard id={MODAL_NO_GEODATA} />}
        {this.props.activeModal === MODAL_RANK_UP ? RankUp(() => {this.close()}, this.props.modalParams) : <ModalCard id={MODAL_RANK_UP} />}
      </ModalRoot>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
	return state.modal ? {
    activeModal: state.modal.activeModal,
    modalParams: state.modal.modalParams,
  } : {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		onOpenModal: (activeModal) =>
			new Promise((resolve) =>
				openModal(activeModal).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Modal);
