import React from 'react';
import { Panel, PanelHeaderSimple, PanelHeaderButton, Button, List, Group, Cell, Div, Header, InfoRow, Progress } from '@vkontakte/vkui';

import { MODAL_REWARD, MODAL_SKIPPED, MODAL_NOT_HOME, MODAL_NO_GEODATA, MODAL_RANK_UP } from '../modals/Modal';

import bridge from '@vkontakte/vk-bridge';
import Api from '../core/api/Api';

// Redux
import { connect } from 'react-redux';
import { updateLevel } from '../redux/actions/level';
import { openModal } from '../redux/actions/modal';

import DangerMeter from '../components/DangerMeter';

import Icon24Poll from '@vkontakte/icons/dist/24/poll';
import Icon24ThumbUp from '@vkontakte/icons/dist/24/thumb_up';
import Icon24ThumbDown from '@vkontakte/icons/dist/24/thumb_down';
import Icon24RecentOutline from '@vkontakte/icons/dist/24/recent_outline';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import '../styles/header.css';
import './Level.css';


class Level extends React.Component {

	constructor(props) {
    super(props);

		this.state = {timer: null};
  }

	componentDidMount() {
		// Таймер обновления (чтобы кнопка "Я дома" появилась если приложение открыто)
		this.setState({timer: window.setInterval(() => {
			Api.home.level(this.props.fetchedUser.id).then((level) => this.props.onLevelUpdate(level));
		}, 60000)});
	}

	componentWillUnmount() {
		window.clearInterval(this.state.timer);
  }

	/** Подтвердить находение дома */
	confirm() {
		bridge.send("VKWebAppGetGeodata", {})
			.then((data) => {
				Api.home.confirm(this.props.fetchedUser.id, data.lat, data.long, (level) => this.afterConfirm(level));
			})
			.catch(() => {
				Api.home.confirm(this.props.fetchedUser.id, null, null, (level) => this.afterConfirm(level));
			});
	}

	afterConfirm(level) {
		this.props.onLevelUpdate(level).then(() => {
			const result = level.result;

			// Получено новое звание
			if(level.rank.rankUp) {
				this.props.onOpenModal(MODAL_RANK_UP, {
					uid: this.props.fetchedUser.id,
					rank: level.rank,
					bonus: level.rank.bonus,
					onLevelUpdate: this.props.onLevelUpdate
				});
			}

			// Обычное подтверждение проверки
			else {

				const status = parseInt(result.status);

				// Подтверждено успешно
				if(status & 1) {
					this.props.onOpenModal(MODAL_REWARD, {value: result.value});
				}

				// Не дома
				else if(status & 2) {
					this.props.onOpenModal(MODAL_NOT_HOME);
				}

				// Опаздал
				else if(status & 4) {
					this.props.onOpenModal(MODAL_SKIPPED, {
						uid: this.props.fetchedUser.id,
						onLevelUpdate: this.props.onLevelUpdate,
						currentValue: result.value,
						promiseValue: level.promiseValue
					});
				}

				// Нет геоданных
				else if(status & 8) {
					this.props.onOpenModal(MODAL_NO_GEODATA);
				}
			}
		});
	}

	statusOfCheck(check) {

		const status = parseInt(check.status);

		if(status & 1) {
			return {
				title: 'Подтверждено',
				icon: (<Icon24ThumbUp style={{color: 'green'}} />)
			};
		} else if(status & 2) {
			return {
				title: 'Не дома',
				icon: (<Icon24ThumbDown style={{color: 'red'}} />)
			};
		} else if(status & 4) {
			return {
				title: 'Поздно',
				icon: (<Icon24RecentOutline style={{color: 'red'}} />)
			};
		} else if(status & 16) {
			return {
				title: 'Пропущено',
				icon: (<Icon24Place style={{color: 'gray'}} />)
			};
		} else if(status & 8) {
			return {
				title: 'Нет геоданных',
				icon: (<Icon24ThumbDown style={{color: 'gray'}} />)
			};
		} else {
			return {
				title: 'Требует подтверждения',
				icon: (<Icon24Notification style={{color: 'var(--button_primary_background)'}} />)
			};
		}
	}

	levelName() {
		const city = this.props.fetchedUser.city && this.props.fetchedUser.city.title ? ' г.' + this.props.fetchedUser.city.title : '';
		return city;
	}

	render() {
		return (
			<Panel id={this.props.id} separator={false}>

				<PanelHeaderSimple left={<PanelHeaderButton onClick={() => {this.props.navigate('rating')}}><Icon24Poll/></PanelHeaderButton>}>
					<b>#</b><b>Л</b><b>У</b><b>Ч</b><b>Ш</b><b>Е</b><b>Д</b><b>О</b><b>М</b><b>А</b>
				</PanelHeaderSimple>

				{this.props.fetchedUser && <Group>
					<List className="Info">
						<Cell
							//before={this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null}
							//description={this.levelName()}
						>
							{/*<Div>{`${this.props.fetchedUser.first_name} ${this.props.fetchedUser.last_name}`}</Div>*/}
							{/*this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null*/}
							<DangerMeter group={this.props.level.group || Math.round(Math.random()*6)} level={this.props.level.value} />
						</Cell>
						<Cell>
						<Div className="Rank">
								<InfoRow header={this.props.level.rank.name}>
			            <Progress value={this.props.level.rank.progress} />
									<div className="InfoRow__footer">{this.props.level.rank.nextName}</div>
			          </InfoRow>
							</Div>
						</Cell>
					</List>
				</Group>}

				{this.props.level && this.props.level.checks && <Group>
					{this.props.level.noGeodata && <Div><Button size="xl" before={<Icon24Place/>} onClick={() => {this.props.permission()}} mode="destructive">Разрешить геоданные</Button></Div>}
					{this.props.level.checks.length > 0 && !this.props.level.checks[0].confirmed && <Div><Button size="xl" mode="commerce" onClick={()=>{this.confirm()}}>Я дома</Button></Div>}
					{/*<Div><Button size="xl" onClick={() => {this.props.onOpenModal(MODAL_REWARD, {value: 555})}}>Reward</Button></Div>*/}
					<Header>Последние проверки</Header>
					<List className="ChecksList">
						<Cell before={<Icon24ThumbUp style={{color: 'green'}} />} key="check_head">
							<Div>Время</Div>
							<Div>Результат</Div>
							<Div className="Values">Баллы</Div>
						</Cell>
						{this.props.level.checks.length === 0 && <Cell key="check_empty" style={{textAlign: 'center'}}><Div>Проверок ещё не было</Div></Cell>}
						{this.props.level.checks.map((check) => {
              return (
								<Cell key={"check_" + check.check_id} before={this.statusOfCheck(check).icon}>
			        		<Div>
										{check.sended}
									</Div>
									<Div>
										{this.statusOfCheck(check).title}
									</Div>
									<Div className="Values" style={check.value > 0 ? {color: 'green', fontWeight: 'bold'} : {}}>
										{check.value}
									</Div>
								</Cell>
							);
						})}
					</List>
				</Group>}
			</Panel>
		);
}}

const mapStateToProps = (state, ownProps) => {
	return {
		level: state.level.level
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		// Обновляет очки и таблицу проверок
		onLevelUpdate: (level) =>
			new Promise((resolve) =>
				updateLevel(level).then((action) => {
					dispatch(action);
					resolve();
				})
			),
		// Открывает модальное окно
		onOpenModal: (activeModal, modalParams) =>
			new Promise((resolve) =>
				openModal(activeModal, modalParams).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Level);
