/**
 * Действия связанные с разрешениями
 */

/**
 * Обновление разрешения
 *
 * @param  {Object} permission
 * @return {Promise}
 */
export const updatePermission = (permission) => {
  return new Promise((resolve, reject) => {
      resolve({
        type: 'PERMISSION_UPDATE',
        permission: permission
      });
  });
};
