import React from 'react';
import { PieChart, Pie, Sector, Cell } from 'recharts';
import Colors from '../constants/Colors';

const data = [
  {name: 'Минимальная', value: 1},
  {name: 'Невысокая', value: 1},
  {name: 'Умеренная', value: 1},
  {name: 'Очень большая', value: 1},
  {name: 'Критическая', value: 1},
  {name: 'Максимальная', value: 1}
];
const COLORS = [Colors.palette1, Colors.palette6, Colors.palette4, Colors.palette9, Colors.palette5, Colors.palette2];

// Корректировка положения стрелочки
const gft = {
  1: [0, -3],
  2: [-5, -8],
  3: [-9, -12],
  4: [9, -12],
  5: [7, -8],
  6: [2, -4]
};

class DangerMeter extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      width: null,
      height: null
    }
  }

  renderActiveShape = (props) => {
    const RADIAN = Math.PI / 180;
    const {
      cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
      fill, payload
    } = props;

    const sin = Math.sin(-RADIAN * midAngle);
    const cos = Math.cos(-RADIAN * midAngle);
    const mx = cx + (innerRadius - 22) * cos + gft[this.props.group][0];
    const my = cy + (innerRadius - 22) * sin + gft[this.props.group][1];
    const ex = mx + (cos >= 0 ? 1 : -1) * 10;
    const ey = my;
    return (
      <g>
        <text x={cx} y={cy-16} textAnchor="middle" fill={Colors.palette10} style={{fontSize: '24px'}}>{this.props.level}</text>
        <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>{payload.name}</text>
        <Sector
          cx={cx}
          cy={cy}
          innerRadius={innerRadius}
          outerRadius={outerRadius}
          startAngle={startAngle}
          endAngle={endAngle}
          fill={fill}
          />
        <g transform={`translate(${ex + (cos >= 0 ? 1 : -1) * 2} ${ey})`}>
          <g transform={`rotate(${-midAngle + 180})`}>
            {/* Draw the triangle with custom shape */}
            <polygon points="8,-8 8,8 -8,0" style={{fill: fill}}/>
          </g>
        </g>
      </g>
    );
  }

	render () {
  	return (
      <div
        className="Div"
        // Размер во всю ширину и высоту, но не меньше 104 по высоте
        ref={el => {
          if (!el || this.state.width) return;
          let height = el.getBoundingClientRect().height;
          this.setState({
            width: el.getBoundingClientRect().width,
            height: height < 104 ? 104 : height
          });
        }}
      >
      	{this.state.width && <PieChart width={this.state.width} height={this.state.height} onMouseEnter={this.onPieEnter}>
          <Pie
            data={data}
            startAngle={180}
            endAngle={0}
            innerRadius={70}
            outerRadius={90}
            fill="#8884d8"
            paddingAngle={5}
            //label={(r) => {return r.name;}}
            cy={85}
            activeIndex={6 - this.props.group}
            activeShape={this.renderActiveShape}
          >
          	{
            	data.map((entry, index) => <Cell fill={COLORS[index % COLORS.length]} key={index}/>)
            }
          </Pie>
        </PieChart>}
      </div>
    );
  }
}

export const getGroupName = (group, withCaption) => {
  let index = (data.length - group)%data.length;
  return (<div>
    {withCaption && <span>Опасность: </span>}
    <span style={{color: COLORS[index]}}>{data[index].name}</span>
  </div>);
};

export default DangerMeter;
