import React from 'react';
import { ModalCard } from '@vkontakte/vkui';

import bridge from '@vkontakte/vk-bridge';
import Api from '../core/api/Api';

import {MODAL_SKIPPED} from './Modal';

import Icon56RecentOutline from '@vkontakte/icons/dist/56/recent_outline';

export default (modalClose, modalParams) => {

  modalParams = modalParams || {};

  // Наборы кнопок
  const setsOfActions = {
    // Возможность анулировать опоздание
    withCancelLate: [{
      title: 'Опубликовать и получить '+modalParams.promiseValue+' баллов',
      mode: 'primary',
      action: () => {
        bridge
          .send("VKWebAppShowWallPostBox", {
            message: "Привет, друзья! Я хочу чтобы эпидемия закончилась как можно скорее, поэтому вношу посильный вклад в борьбу с короновирусом!\nЭто совсем несложно, присоединяйтесь!\n#лучшедома #сидитедома #covid19",
            attachments: 'https://vk.com/app7405679'
          })
          .then(() => Api.home.cancelLate(modalParams.uid, (level) => modalParams.onLevelUpdate(level).then(modalClose)));
      }
    },{
      title: 'Оставить '+modalParams.currentValue+' баллов',
      mode: 'secondary',
      action: modalClose
    }],
    // Без анулирования
    simple: [{
      title: modalParams.currentValue+' баллов',
      mode: 'primary',
      action: modalClose
    }]
  }

  return (<ModalCard
    id={MODAL_SKIPPED}
    onClose={modalClose}
    icon={<Icon56RecentOutline style={{color: 'red'}} />}
    header="Вы опоздали! Прошло больше 20 минут.."
    caption={
      modalParams.promiseValue > modalParams.currentValue ?
        "Поделитесь важной информацией о Covid-19 с друзьями, чтобы не потерять прогресс!" :
        "Вы получите больше баллов если будите вовремя подтверждать запросы."
    }
    actions={setsOfActions[modalParams.promiseValue > modalParams.currentValue ? 'withCancelLate' : 'simple']}
  >
  </ModalCard>);
};
