import React from 'react';
import { ModalCard } from '@vkontakte/vkui';

import bridge from '@vkontakte/vk-bridge';
import Api from '../core/api/Api';

import {MODAL_RANK_UP} from './Modal';

import rankUpPng from '../img/rankup.png';

export default (modalClose, modalParams) => {

  modalParams = modalParams || {rank: {name: ''}};

  return (<ModalCard
    id={MODAL_RANK_UP}
    onClose={modalClose}
    icon={<img src={rankUpPng} alt="Новое звание" />}
    header={"Достигнуто новое звание «"+modalParams.rank.name+"»"}
    caption="Поздравляем с новым достижением! Мы думаем вы добьетесь большего! Получите дополнительный бонус за публикацию своих достижений в Историях."
    actions={[{
      title: 'Опубликовать и получить ещё '+modalParams.bonus+' баллов',
      mode: 'primary',
      action: () => {
        bridge
    			.send("VKWebAppShowStoryBox", {
    				background_type: "image",
    				url: "https://sun9-56.userapi.com/c855620/v855620203/21b0af/hX1H8hzH848.jpg",
    				attachment: {
    					text: 'open',
    					type: 'url',
    					url: 'https://vk.com/app7405679'
    				}
    			})
          .then(() => Api.home.rankUpBonus(modalParams.uid, (level) => modalParams.onLevelUpdate(level).then(modalClose)));
      }
    },{
      title: 'Закрыть',
      mode: 'secondary',
      action: modalClose
    }]}
  >
  </ModalCard>);
};
