/**
 * Редуктор разрешений
 */

export default function reducer(state = null, action) {
  switch (action.type) {
    // Обновление разрешения
    case 'PERMISSION_UPDATE':
      return Object.assign({}, state, {
        permissions: Object.assign({}, state, action.permission)
      });

    default:
      return state;
    }
}
